import Vue from 'vue'
import Router from 'vue-router'

import DetailInfo from '@/pages/DetailInfo/DetailInfo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/register/detailInfo',
      name:'detailInfo',
      component:DetailInfo
    },
  ]
})



